
// Variables


:root {
	// Breakpoints
    --breakpoint-max: 1180px;
    --breakpoint-min: 900px;
    
    --s-design-min: 375;
    --s-design-max: 900;
	// Light
	--page-color: #ffffff;
	--component-color: #ffffff;
	--dark-component-color: #eff3f5;
	--brand-color: #122131;
	--headings-color: #385672;
	--text-color: #888;
	--light-text-color: #ffffff;
	--link-color: #3d6a93;
	--button-fill: #48b9ea;
	--button-fill-text: #fff;
	--tag-color: #eeeeee;
	--border-color: #bed4df;
	--shadows-color: #f7f7f7;
	--navigation-color: #FEFEFE;
	--footer-color: #191926;
	--footer-link-color: rgb(136, 136, 136);
	--footer-link-hover-color: #ffffff;
	// Dark
	--dark-page-color: #131313;
	--dark-light-component-color: #131313;
	--dark-dark-component-color: #1a1a1a;
	--dark-brand-color: #20ca5a;
	--dark-headings-color: #ffffff;
	--dark-text-color: #959595;
	--dark-light-text-color: #ffffff;
	--dark-link-color: #00a4ca;
	--dark-button-fill: #20ca5a;
	--dark-button-fill-text: #ffffff;
	--dark-tag-color: #444444;
	--dark-border-color: #373737;
	--dark-shadows-color: #060606;
	--dark-navigation-color: #131313;
	--dark-footer-color: #222222;
	--dark-footer-link-color: #ffffff;
	--dark-footer-link-hover-color: #73a9c9;

	--headings-font: Public Sans;
	--body-font: Public Sans;
	--content-font-size: 1.8em;
}


