.c-navigation { $c: &;
	display: flex;
	align-items: center;
	position: absolute;
	z-index: 4;
	top: 0;
	left: 0;
	padding: 17px 20px;
	width: 100%;
	height: 70px;
	max-height: 70px;
	background-color: var(--navigation-color);
	// box-sizing: content-box;

	&--open {
		@media (max-width: 700px) {
			position: relative;
			margin-top: -70px;
			height: auto;
			max-height: 1200px;

			#{$c}__nav-items {
				padding-top: 10px;
			}

			#{$c}__item-wrapper {
				height: auto;
			}
		}

		#{$c}__item {
			opacity: 1;
			pointer-events: all;
		}
	}

	&__title {
		display: none;
	}

	&__inner-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 auto;
		width: 100%;
		max-width: 1140px;

		@media (max-width: 700px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__nav-items {
		display: flex;
		align-items: center;
		margin: 0;
		padding: 0;
		list-style: none;


		@media (max-width: 700px) {
			flex-direction: column;
			align-items: flex-start;
			padding-top: 0;
			width: 100%;
		}
	}

	&__item-wrapper {
		display: flex;
		font-size: 10px;

		@media (max-width: 700px) {
			width: 100%;
			height: 0;
			overflow: hidden;
		}

		&--mobile {

		}
	}

	&__item {
		margin: 0 3px;
		padding: 10px;
		border-bottom: 1px solid rgba(255, 255, 255, 0);
		color: rgba(255, 255, 255, 0.8);
		transition: color .2s ease-in-out, border-bottom .2s ease-in-out;
		text-decoration: none;
		font-size: 1.3em;
		font-weight: 500;

		@media (max-width: 700px) {
			margin: 0 3px;
			padding: 20px 10px;
			width: 100%;
			border-bottom: 1px solid rgba(255, 255, 255, 0.1);
			opacity: 0;
			pointer-events: none;
		}

		&:hover {
			border-bottom: 1px solid rgba(255, 255, 255, 0.3);
			text-decoration: none;
		}

		&--active {
			color: var(--light-text-color);
		}

		&--button {
			display: flex;
			border: 1px #ccc solid;
			border-radius: 5px;

			@media (max-width: 700px) {
				border: none;
				border-bottom: 1px solid rgba(255, 255, 255, 0.1);
				border-radius: 0;
			}

			&:hover {
				background: #fff;
				color: var(--brand-color);
				@media (max-width: 700px) {

					background: unset;
					color: rgba(255, 255, 255, 0.8);
				}
			}
		}

		&--mobile-toggle {
			display: none;
			margin-top: -6px;
			font-size: 2.8em;
			width: auto;
			opacity: 1;
			pointer-events: all;

			@media (max-width: 700px) {
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				border: 1px solid transparent;
			}

			&:hover {
				border-bottom: 1px solid transparent;
			}
		}
	}

	&__label {
		text-transform: uppercase;
	}

	img {
		width: 100%;
		max-width: 95px;
		object-fit: contain;

		&.logo{
			max-width: 200px;
		}
	}
}

// These styles are the base styles for all themes. They are also the base set for Hydra. 
// Changing these will effect all themes, and the Hydra theme.
// To add a new theme, create a new stylesheet and include it at the bottom of this one.


* {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    box-sizing: border-box;
    text-rendering: geometricPrecision;
}

html {
    scroll-behavior: smooth;
    font-size: 10px;
}

html,
body {
    --font: var(--body-font), "Helvetica Neue", "Helvetica", "Arial";
    --headings-fonts: var(--headings-font), "Helvetica Neue", "Helvetica", "Arial";
    --component-padding: 100px;
    --navigation-offset: 70px;
    position: relative;
    margin: 0;
    font-family: var(--font);
    color: var(--text-color);
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;

    h1, h2, h3, h4, h5 {
        color: var(--headings-color);
        font-family: var(--headings-fonts);
    }

    h1 {
        margin: 0;
        font-weight: 300;
        font-size: 3.7em;
    }

    h2, .archive-layout__heading {
        font-weight: 400;
        font-size: 3.6em;
        margin: 0 0 30px 0;
    }

    h3 {
        margin: 0;
        font-size: 1.9em;
        font-weight: 400;
    }

    h4 {
        margin: 20px 0 10px 0;
        font-size: 1.6em;
        font-weight: 300;
    }

    p, li, blockquote, table {
        font-size: var(--content-font-size);
        font-weight: 400;
    }

    p {
        color: var(--text-color);
        margin-bottom: 20px;
        line-height: 140%;
    }

    img {
        max-width: 100%;
        height: auto;
        object-fit: contain;
    }

    strong {
        font-weight: 700;
    }

    a {
        text-decoration: none;
        color: var(--link-color);

        &:hover {
            text-decoration: underline;
        }
    }

    blockquote {
        margin: 25px var(--s-30-50) 25px var(--s-30-50);
        padding: 15px 15px 15px 45px;
        box-sizing: border-box;
        border-left: 4px solid rgba(0,0,0,0.2);
        background-color: rgba(0,0,0,0.04);
    }

    pre {
        padding: 10px;
        background: #313131;
        border-radius: 2px;

         code {
            font-size: 1.4em;
        }
    }

    p code, li code {
        font-size: initial;
        padding: 6px 10px;
        background: #313131;
        border-radius: 2px;
    }
}

// Layouts

.body-wrapper {
   padding-top: var(--navigation-offset);
   background-color: var(--page-color);
}

.layout {
    margin: 0 auto;
    padding: var(--component-padding) var(--s-30-50);

    &--post {
        max-width: 750px;
        box-sizing: content-box;

        h1:not(:first-child), h2:not(:first-child), h3:not(:first-child), h4:not(:first-child), h5:not(:first-child), h6:not(:first-child) {
            margin-top: 2.2em;
        }

        .n-post-preview {
            display: none;
        }

        .c-post-navigation {
            &__next-preview {
                display: none;
            }
        }
    }

    &--client {
        padding: 0;
    }
}

.dark-mode-toggle {
    .n-button {
        position: absolute;
        bottom: 15px;
        right: 15px;
        font-size: 1.2em;
        padding: 6px 8px;
        cursor: pointer;
        user-select: none;

        svg {
            padding-right: 8px;
        }
    }
}

// Components

.component-wrapper {
    &--light {
        background-color: var(--component-color);
    }
    &--dark {
        background-color: var(--dark-component-color);
    }

    &--branded {
        background-color: var(--brand-color);
    }

    &:last-child {
        // padding-bottom: calc(var(--component-padding) + 20px);
    }
}

.component {
    position: relative;
    margin: 0 auto;
    padding: var(--component-padding) var(--s-30-50);
    max-width: var(--breakpoint-max);
    transition: filter 0.2s ease-out, opacity 0.2s ease-in-out; // For CMS editor links
    box-sizing: content-box;

    &:before, &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: inherit;
    }

    &:after {
        left: unset;
        right: -100%;
    }

    &--full {
        padding-left: 0;
        padding-right: 0;
        max-width: none;
    }
}
.c-footer { $c: &;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 50px var(--s-30-50);
	// padding: calc(50px + var(--component-padding)) 
	// 		 var(--s-30-50) 
	// 		 50px 
	// 		 var(--s-30-50);
	background-color: var(--footer-color);

	&__columns {
	    display: grid;
	    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	    grid-gap: 25px;
		width:100%;
		max-width: var(--breakpoint-max);
	}

	&__column {
		h4 {
			font-size: 2.5em;
			margin: 15px auto;
			max-width: 150px;
			color: var(--text-color);
			font-weight: 600;
		}

		ul {
			margin: 0;
			padding: 0;
			list-style: none;
		}

		@media (max-width: 599px) {
			margin: 10px 0;
		}

		li {
			margin: 15px auto;
			max-width: 150px;
			font-size: 1.8em;

			@media (max-width: 599px) {
				margin: 15px 0;
			}
		}

		a {
			&:hover {
				color: var(--footer-link-hover-color);
			}
		}
	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		color: var(--footer-link-color);
		font-weight: 500;

		@media (max-width: 599px) {

			#{$c}__icon {
				position: relative;
				left: unset;
			}
		}
	}

	&__link-label {

	}

	&__icon {
		position: absolute;
		left: -32px;
		padding-right: 8px;
		max-width: 32px;
		max-height: 32px;
	}

	&__lower-text {
		margin-top: 10px;
		
		p {
			color: var(--footer-link-color);
			font-size: 1.4em;
			font-weight: 400;

			a {
				color: var(--footer-link-color);
				text-decoration: none;
				font-weight: 600;

				&:hover {
					color: var(--footer-link-hover-color);
				}
			}
		}
	}
}
.archive-layout {
	margin: 0 auto;
	background: var(--page-color);

	&__heading {
		padding: var(--component-padding) var(--s-30-50) 0 var(--s-30-50);
		text-align: center;
	}

	&__tags {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		align-items: center;
		margin-top: 25px;
	}

	&__tag {
		display: flex;
		padding: 3px 5px;
		border-radius: 5px;
		background: var(--tag-color);

		a {
			font-size: 12px;
			color: var(--text-color);

			&:hover {
				text-decoration: none;
			}
		}
	}
}

