}

.c-footer {
	padding-bottom: 0;

	@media (max-width: 650px) {
		padding: 50px var(--s-30-50);
	}

	&__column {
		h4 {
			max-width: none;
		}
		
		li {
			padding: 0 25px 0 0;
			max-width: 313px;
			font-size: 1.4em;
		}
	}

	&__link:hover {
		text-decoration: none;
	}

	&__lower-text {
		margin-top: 75px;
	}
}

